<template>
    <section class="post"
             title="Рандомно сгенерированный пост">
        <div class="post__content">
            <h3 class="post__title"><strong>Название: </strong>{{ post.title }}</h3>
            <div class="post__text"><strong>Описание: </strong><p>{{ post.body }}</p></div>
            <div class="post__id"><strong>#</strong>{{ post.id }}</div>
        </div>
        <div class="post__action">
            <my-button class="post__change" @click="$router.push(`/posts/${post.id}`)">Открыть пост</my-button>
            <my-button class="post__change" @click="$emit('changePost', this.post)">Изменить пост</my-button>
            <my-button class="post__delete" @click="$emit('deletePost', this.post)">Удалить пост</my-button>
        </div>
    </section>
</template>

<script>

export default {
    name: 'post-item',
    props:{
        post:{
            type: Object,
            required:true
        }
    }
}
</script>

