<template>
    <section class="post-form">
        <h2>Создать новый пост:</h2>
        <section class="post-form__container" title="Настройки применяются автоматически.">
            <label class="post-form__text">
                Название:
            </label>
            <my-input
                class="post-form__input"
                :type="'text'"
                v-model="post.title"
            />
            <label class="post-form__text">
                Описание:
            </label>
            <my-input
                class="post-form__input"
                :type="'text'"
                v-model="post.body"
            />
            <div class="post-form__actions">
                <my-button v-if="changeMode" @click="$emit('closeDialog')">Отмена</my-button>
                <my-button v-if="changeMode" @click="$emit('changePost', post)">Сохранить </my-button>
                <my-button v-else @click="$emit('createPost', this.post)">Создать пост</my-button>
            </div>
        </section>
    </section>
</template>

<script>

export default {
    name: "post-form",
    props: {
        changeMode:{
            type: Boolean,
            default: false
        },
        postToChange:{
            type: Object
        }

    },
    data(){
        return {
            post:{
                userId: 1,
                id: 1,
                title: '',
                body: ''
            }
        }
    },
    methods:{
        loadPost(){
            if (this.postToChange !== undefined){
                this.post = {...this.postToChange};
            }
        },
    },
    mounted() {
        this.loadPost();
    }
}
</script>

<style lang="scss" scoped>
.post-form{
    min-width: 600px;

    &__container{
        width: 100%;

        margin-top: 20px;
        padding: 2em;

        border-radius: 20px;
        background: rgba(255, 255, 255, 0.1);
        color: rgba(255, 255, 255, 0.4);
        text-align:left;
        user-select: none;

    }
    &__input{
        margin: 10px 0;
    }
    &__text{
    }
    &__actions{
        margin-top: 10px;
        display: flex;
        justify-content: flex-end;
        gap: 20px;
    }
    @media screen and (max-width: 1100px) {
        min-width: 400px;
    }
}


</style>