<template>
    <div class="wrapper">
        <my-dialog v-model:show="createDialogVisible">
            <post-form
                class="app__post-form"
                @createPost="createPost"
            />
        </my-dialog>
        <my-dialog v-model:show="changeDialogVisible">
            <post-form
                class="app__post-form"
                @changePost="(post) => {
                    this.posts[this.posts.indexOf(this.posts.find(p => p.id === post.id))] = post;
                    this.changeDialogVisible = false;
                }"
                @closeDialog="changeDialogVisible = false"
                :change-mode="true"
                :post-to-change="postToChange"
            />
        </my-dialog>

        <div class="app__toolbar toolbar">
            <my-button
                class="toolbar__item"
                @click="showCreateDialog"
            >Создать пост</my-button>
            <my-select
                class="toolbar__item"
                v-model="selectedSort"
                :options="sortOptions"
            >Сортировать по:</my-select>
            <my-input placeholder="Поиск"
                      v-model="searchQuery"
            />

        </div>
        <post-list
            v-if="!isPostsLoading"
            class="app__post-list"
            @changePost="showChangeDialog"
            @deletePost="deletePost"
            @createPost="showCreateDialog"
            :posts="sortedAndSearchedPosts"
        />
        <div class="pagination"
             v-if="!isPostsLoading"
        >
            <div
                class="pagination__item"
                v-for="page in totalPages"
                :key="page"
                @click="setPage(page)"
                :class="{
                    'pagination__item_active': page === this.page
                }"
            > {{ page }} </div>
        </div>
        <div v-else class="posts-loading">Идёт загрузка постов...</div>

    </div>
</template>

<script>
import postList from '@/components/PostList';
import postForm from "@/components/PostForm";
import {mapState, mapMutations, mapGetters, mapActions} from 'vuex';
import {ref, watch} from "vue";
import {usePosts} from "@/hooks/usePosts";
import {useSortedPosts} from "@/hooks/useSortedPosts";
import {useSortedAndSearchedPosts} from "@/hooks/useSortedAndSearchedPosts";

export default {
    components:{
        postList, postForm
    },
    data(){
        return {
            createDialogVisible: false,
            changeDialogVisible: false,
            postToChange: {},
            sortOptions:[
                {value: 'title', name: 'По названию'},
                {value: 'body', name: 'По содуржанию'},
            ],
        }
    },
    methods: {
        setPage(page){
            this.page = page;
        },
        createPost(post){
            post.id = Date.now();
            this.posts.unshift(post);
            this.createDialogVisible = false;
        },
        changePost(post){
            this.posts[state.posts.indexOf(state.posts.find(p => p.id === post.id))] = post;
            this.changeDialogVisible = false;
        },
        deletePost(post){
            this.posts = this.posts.filter(p => p.id !== post.id)
            this.show = true;
        },
        showCreateDialog(){
            this.createDialogVisible = true;
        },
        showChangeDialog(post){
            this.postToChange = post;
            this.changeDialogVisible = true;
        },
    },
    mounted() {

    },
    setup(props){
        const {page, isPostsLoading, totalPages, posts, fetching} = usePosts( 10);
        const {selectedSort, sortedPosts} = useSortedPosts(posts);
        const {searchQuery, sortedAndSearchedPosts} = useSortedAndSearchedPosts(sortedPosts);
        watch(page, fetching);
        watch(posts, ()=>{console.log('Posts is changed')})
        return{
            page, isPostsLoading, posts, totalPages, fetching,
            selectedSort, sortedPosts,
            searchQuery, sortedAndSearchedPosts
        }
    },
    watch: {
        page() {

        }
    }
};
</script>

<style lang="scss" scoped>

.toolbar{
    display: flex;
    flex-direction: row;
    gap: 10px;

    &__item{
        display: inline-block;
        width: 100%;
    }
}


.wrapper{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

    width: 100%;
    height: 100%;
    max-height: calc(100vh - 76px);
    max-width: 1100px;

    padding: 20px;

}
.pagination{
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: space-between;
    &__item{
        cursor: pointer;
        padding: 10px;
        &_active{
            color: lightseagreen;
        }
    }
}

</style>
