<template>
    <input :value='modelValue' :type='type' @input='updateInput'>
</template>

<script>
export default {
    name: "my-input",
    props: {
        type:{
            type: String
        },
        modelValue: [String, Number]
    },
    methods:{
        updateInput(e){
            this.$emit('update:modelValue', e.target.value);
        }
    }
}
</script>

<style lang="scss" scoped>
input{
    width: 100%;

    background-color: #3e5370;
    border-radius: 0.5em;
    padding: 10px;
    font-weight: bold;
    color: #1cdbd1;
    //color: #d3f6f0;
    &::placeholder{
        color: rgba(36, 255, 245, 0.3);
    }
}
</style>