import axios from "axios";

export const postModule = {
    state: () => ({
        posts: [],
        post: {},
        postToChange: {},
        isPostsLoading: false,
        selectedSort: '',
        page: 1,
        limit: 10,
        totalPages: 0,
        sortOptions:[
            {value: 'title', name: 'По названию'},
            {value: 'body', name: 'По содуржанию'},
        ],
        searchQuery: '',
    }),
    getters:{
        getPostById: (state) => (id) => {
            return state.posts.find(post => post.id === +id)
        },
        sortedPosts(state){
            return [...state.posts].sort((post1, post2) => post1[state.selectedSort]?.localeCompare(post2[state.selectedSort]));
        },
        sortedAndSearchedPosts(state, getters){
            return getters.sortedPosts.filter(p => (p.title+' '+p.body).toLowerCase().includes(state.searchQuery.toLowerCase()));
        },
    },
    mutations:{
        setPosts(state, posts) {
            state.posts = posts;
        },
        createPost(state, post){
            post.id = Date.now();
            state.posts.unshift(post);
            state.createDialogVisible = false;
        },
        deletePost(state, post){
            state.posts = state.posts.filter(p => p.id !== post.id)
            state.show = true;
        },
        changePost(state, post){
            state.posts[state.posts.indexOf(state.posts.find(p => p.id === post.id))] = post;
            state.changeDialogVisible = false;
        },
        setPost(state, post) {
            state.post = post;
        },
        setPostToChange(state, post) {
            state.postToChange = post;
        },
        setLoading(state, bool) {
            state.isPostsLoading = bool;
        },
        setSelectedSort(state, string) {
            state.selectedSort = string;
        },
        setPage(state, page) {
            state.page = page;
        },
        setTotalPages(state, int) {
            state.totalPages = int;
        },
        setSearchQuery(state, string) {
            state.searchQuery = string;
        }
    },
    actions: {
        async fetchPosts({state, commit}) {
            try {
                commit('setLoading', true);
                const response = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    params: {
                        _page: state.page,
                        _limit: state.limit,
                    }
                });
                let newTotalPages = Math.ceil(response.headers['x-total-count'] / state.limit);
                commit('setTotalPages', newTotalPages);
                commit('setPosts', response.data);
            } catch (e) {
                alert('Ошибка!');
                console.log(e)
            } finally {
                commit('setLoading', false);
            }
        },
        async fetchPostById({state, commit}, id) {
            try {
                commit('setLoading', true);
                const response = await axios.get('https://jsonplaceholder.typicode.com/posts/'+id);
                let newTotalPages = Math.ceil(response.headers['x-total-count'] / state.limit);
                commit('setPost', response.data);
            } catch (e) {
                alert('Ошибка!');
                console.log(e)
            } finally {
                commit('setLoading', false);
            }
        }
    },
    namespaced: true
}

