<template>
    <div v-if="show" class="my-dialog">
        <div class="my-dialog__body">
            <slot></slot>
        </div>
        <div class="full-screen-action" @click="hideDialog">
            <div  class="my-dialog__cross cross cross_hover" @click="hideDialog">
                <span></span>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: "my-dialog",
    props:{
        show:{
            type: Boolean,
            default: false
        }
    },
    methods:{
        hideDialog(){
            this.$emit('update:show', false);
        },
    },
}
</script>

<style lang="scss" scoped>
.my-dialog{
    position: absolute;
    left: 0;
    top: 0;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    z-index: 10;

    width: 100%;
    height: 100%;

    background-color: rgba(31, 41, 68, 0.3);
    &__body{
        max-height: 90vh;
        max-width: 90vw;
        padding: 1.5em 2em;

        background-color: #222436;
        border-radius: 0.5em;
        cursor: default;
        user-select: none;
        overflow: auto;

        z-index: 20;

        &::-webkit-scrollbar{
            width: 5px;
            &-thumb{
                border-radius: 3px;
                background-color: lightseagreen;
            }
        }
    }

    &__cross{
        display: flex;
        justify-content: center;
        flex-direction: column;
        position: absolute;
        top: 30px;
        right: 30px;

        width: 30px;
        height: 30px;

        cursor: pointer;

        & span, & span:before{
            position: absolute;
            display: block;

            width: 100%;
            height: 5px;

            background: #fff;
            transition: 0.3s;
            border-radius: 3px;

            content: '';

        }

    }
    .full-screen-action{
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;

        width: 100%;
        height: 100%;

        z-index: 11;

        cursor: pointer;

        &:hover .my-dialog__cross span{
            transform: rotate(45deg);
            &:before {
                transform: rotate(90deg);
            }
        }
    }
    h1{
        color: lightskyblue;
        display: block;
    }
}
</style>