<template>
    <!--    TODO: test -->
    <!--    FIXME: test -->
    <div class="wrapper">
        <my-dialog v-model:show="createDialogVisible">
            <post-form
                class="app__post-form"
                @createPost="createPost"
            />
        </my-dialog>
        <my-dialog v-model:show="changeDialogVisible">
            <post-form
                class="app__post-form"
                @changePost="saveChangesAndClose"
                @closeDialog="changeDialogVisible = false"
                :change-mode="true"
                :post-to-change="postToChange"
            />
        </my-dialog>

        <div class="app__toolbar toolbar">
            <my-button
                class="toolbar__item"
                @click="showCreateDialog"
            >Создать пост</my-button>
            <my-select
                class="toolbar__item"
                :model-value="selectedSort"
                @update:model-value="setSelectedSort"
                :options="sortOptions"
            >Сортировать по:</my-select>
            <my-input placeholder="Поиск"
                      :model-value="searchQuery"
                      @update:model-value="setSearchQuery"
            />

        </div>
        <post-list
            v-if="!isPostsLoading"
            class="app__post-list"
            @changePost="showChangeDialog"
            @deletePost="deletePost"
            @createPost="showCreateDialog"
            :posts="sortedAndSearchedPosts"
        />
        <div class="pagination"
             v-if="!isPostsLoading"
        >
            <div
                class="pagination__item"
                v-for="page in totalPages"
                :key="page"
                @click="setPage(page)"
                :class="{
                    'pagination__item_active': page === this.page
                }"
            > {{ page }} </div>
        </div>
        <div v-else class="posts-loading">Идёт загрузка постов...</div>

    </div>
</template>

<script>
import postList from '@/components/PostList';
import postForm from "@/components/PostForm";
import {mapState, mapMutations, mapGetters, mapActions} from 'vuex';

export default {
    components:{
        postList, postForm
    },
    data(){
        return {
            createDialogVisible: false,
            changeDialogVisible: false,
        }
    },
    methods: {
        ...mapMutations({
            setPage: 'post/setPage',
            setPostToChange: 'post/setPostToChange',
            setSearchQuery: 'post/setSearchQuery',
            setSelectedSort: 'post/setSelectedSort',
            createPost: 'post/createPost',
            deletePost: 'post/deletePost',
            changePost: 'post/changePost',
        }),
        ...mapActions({
            fetchPosts: 'post/fetchPosts'
        }),
        log(e){
            console.log(e);
        },
        saveChangesAndClose(post){
            this.$store.commit('post/changePost', post);
            this.changeDialogVisible=false;
        },
        showCreateDialog(){
            this.createDialogVisible = true;
        },
        showChangeDialog(post){
            this.setPostToChange(post);
            this.changeDialogVisible = true;
        },

    },
    mounted() {
        this.fetchPosts()
    },
    computed:{
        ...mapState({
            posts: state => state.post.posts,
            postToChange: state => state.post.postToChange,
            isPostsLoading: state => state.post.isPostsLoading,
            selectedSort: state => state.post.selectedSort,
            page: state => state.post.page,
            limit: state => state.post.limit,
            totalPages: state => state.post.totalPages,
            sortOptions:state => state.post.sortOptions,
            searchQuery: state => state.post.searchQuery,
        }),
        ...mapGetters({
            sortedPosts: 'post/sortedPosts',
            sortedAndSearchedPosts: 'post/sortedAndSearchedPosts',
        }),
    },
    watch: {
        page(){
            this.fetchPosts()
        }
    },
};
</script>

<style lang="scss" scoped>

.toolbar{
    display: flex;
    flex-direction: row;
    gap: 10px;

    &__item{
        display: inline-block;
        width: 100%;
    }
}


.wrapper{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

    width: 100%;
    height: 100%;
    max-height: calc(100vh - 76px);
    max-width: 1100px;

    padding: 20px;

}
.pagination{
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: space-between;
    &__item{
        cursor: pointer;
        padding: 10px;
        &_active{
            color: lightseagreen;
        }
    }
}

</style>
