<template>
    <select v-model="modelValue" @change="changeOption">
        <option disabled selected value=""><slot></slot></option>
        <option
            v-for="option in options"
            :key="option.value"
            :value="option.value"
            >
            {{ option.name }}
        </option>
    </select>
</template>

<script>
export default {
    name: "my-select",
    props:{
        modelValue:{
          type: String
        },
        options:{
            type: Array,
            default: () => [],
        }
    },
    methods:{
        changeOption(e){
            this.$emit('update:modelValue', e.target.value);
        },

    },
}
</script>

<style lang="scss" scoped>
select{
    display: inline-block;

    min-height: 30px;
    width: 100%;

    background-color: #3e5370;
    border-radius: 0.5em;
    padding: 10px;
    font-weight: bold;
    color: #1cdbd1;
    word-wrap: normal;
    text-align: center;
    text-overflow: ellipsis;

    & option{
        font-size: 14px;
        &:disabled{
            color: #d3f6f0;
        }
    }
}
</style>